.intro {
  height: 795px;
  background-image: radial-gradient(circle at 0 0, #0c1629, #0c1629, #010203);
  // background-image: radial-gradient(top left #0c1629 bottom right #010203)
  @include center;
  position: relative;
  padding-top: 100px;
  @include r(767) {
    @include center-column;
    height: auto;
    flex-direction: column-reverse;
    padding-top: 120px; }
  // &:before
  //   content: ''
  //   position: absolute
  //   height: 100%
  //   width: 100%
  //   left: 0
  //   top: 0
  //   background: rgba(#131c2e, 0.92)
  //   z-index: 2
  // +r(767)
  //   height: 650px
  // video
  //   position: absolute
  //   top: 0
  //   left: 50%
  //   transform: translate(-50%)
  //   height: 100%
  //   z-index: 1
  //   +r(767)
  //     display: none
  //   +rmin(1400)
  //     width: 100%
  //     height: auto
  //     left: 0
  //     top: 50%
  //     transform: translate(0, -50%)

  .container {
    position: relative;
    z-index: 3; }
  &__img {
    position: absolute;
    right: 0;
    z-index: 3;
    height: 620px;
    width: 100%;
    overflow: hidden;
    @include r(767) {
      position: relative;
      height: auto; }
    .row {
      height: 100%; }
    img {
      height: 100%;
      max-width: 100%;
      @include r(1040) {
        height: auto;
        position: relative;
        top: 50%;
        transform: translate(0, -50%); }
      @include r(767) {
        max-height: 100%;
        left: 0; } } }
  &__title {
    h2 {
      font-size: 40px;
      font-weight: 300;
      line-height: 1.25;
      color: $white;
      margin-bottom: 35px;
      @include r(1040) {
        br {
          display: none; } }
      @include r(767) {
        font-size: 30px;
        text-align: center;
        line-height: 1.65; } }
    p {
      font-size: 14px;
      font-weight: 300;
      line-height: 1.91;
      letter-spacing: 0.6px;
      color: #dfdfdf;
      margin-bottom: 90px;
      max-width: 370px;
      @include r(767) {
        text-align: center;
        margin-bottom: 50px;
        max-width: 100%; } } }
  &__content {
    height: 110px;
    position: relative;
    width: 100%;
    @include r(767) {
      margin-bottom: 40px; } }
  &__btn {
    display: flex;
    align-items: center;
    height: 110px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    @include tr;
    visibility: visible;
    opacity: 1;
    &.no-active {
      visibility: hidden;
      opacity: 0; }
    @include r(767) {
      margin-bottom: 50px;
      justify-content: center; }
    img {
      margin-bottom: 13px; }
    a {
      width: 100px;
      height: 103px;
      border-radius: 10px;
      border: 1px solid #56688b;
      margin-right: 15px;
      @include center-column;
      font-size: 12px;
      line-height: 2.08;
      color: rgba(255, 255, 255, 0.5);
      &:last-child {
        margin-right: 0; }
      @include r(400) {
        height: 85px; }
      &:hover {
        background: #141e33; } } }
  &__form {
    position: relative;
    width: 327px;
    height: 110px;
    position: absolute;
    top: 0;
    left: 0;
    visibility: hidden;
    opacity: 0;
    @include tr;
    &.active {
      visibility: visible;
      opacity: 1; }
    @include r(767) {
      left: 50%;
      transform: translate(-50%); }
    @include r(350) {
      width: 100%; }
    &__close {
      width: 30px;
      height: 30px;
      position: absolute;
      right: 0px;
      top: -8px;
      span {
        &:before, &:after {
          content: '';
          width: 15px;
          height: 3px;
          background: $pinkish-grey;
          position: absolute;
          top: 15px;
          right: 8px;
          @include tr; }
        &:before {
          transform: rotate(45deg); }
        &:after {
          transform: rotate(-45deg); } }
      &:hover {
        span:before, span:after {
          background: $pink-red; } } }
    &__result {
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      visibility: hidden;
      opacity: 0;
      @include tr;
      &.active {
        visibility: visible;
        opacity: 1; }
      @include r(767) {
        margin-bottom: 50px; }
      &__block {
        display: flex;
        align-items: flex-start;
        p {
          font-size: 14px;
          font-weight: 600;
          line-height: 1.43;
          color: #31cd64;
          margin-left: 10px;
          @include r(767) {
            br {
              display: none; } } } } }
    b {
      font-size: 14px;
      font-weight: normal;
      margin-bottom: 20px;
      color: $white;
      display: block;
      @include r(767) {
        text-align: center; } }
    span {
      font-size: 12px;
      color: rgba(255, 255, 255, 0.2);
      display: block;
      margin-top: 10px;
      @include r(767) {
        text-align: center; } }
    &__block {
      display: flex;
      align-items: center;
      height: 47.5px;
      border-radius: 23.8px;
      background-color: #f6f6f6;
      border: 1.5px solid transparent;
      @include tr;
      animation-duration: 0.5s;
      &.error {
        border-color: #fd3e39 !important; }
      .select-tn {
        position: relative;
        display: inline-block;
        margin-right: 15px;
        @include r(767) {
          margin-right: 7px; }
        select {
          display: none; }
        img {
          width: 15px;
          height: 15px;
          margin-left: 20px;
          @include r(767) {
            margin-left: 10px; } }
        p {
          padding-left: 10px;
          font-size: 14px;
          color: $dark-two; }
        li {
          padding: 10px;
          display: flex;
          &.active {
            background: #eee; } }
        &__result {
          display: flex;
          width: 100px;
          padding: 19px 0;
          position: relative;
          @include r(767) {
            width: 88px; }
          &:before {
            content: '';
            position: absolute;
            right: 4px;
            top: 25px;
            border: 8px solid transparent;
            border-top: 8px solid #bdbdbd;
            @include tr; }
          &.active {
            &:before {
              transform: rotate(180deg);
              top: 20px; } } }
        &__dorpdown {
          position: absolute;
          top: 100%;
          z-index: 9;
          width: 100%;
          overflow: hidden;
          display: none;
          background: $white;
          border: 1px solid #bdbdbd;
          img {
            margin-left: 0; } } }
      &__input {
        width: 100%;
        input {
          height: 100%;
          font-size: 14px;
          color: $dark-two;
          @include placeholder-color($dark-two);
          padding: 12px 0;
          width: 100%;
          background: #f6f6f6; } }
      &__submit {
        padding-right: 10px;
        position: relative;
        input {
          position: absolute;
          z-index: 3;
          width: 30px;
          height: 30px;
          opacity: 0;
          right: 9px;
          top: 0;
          display: none; }
        button {
          width: 29px;
          height: 29px;
          opacity: 0.5;
          background-color: $pinkish-grey;
          @include center;
          border-radius: 100%; } } } } }


